import { JestInstance, Describe, It } from './JestInstance.js';
export class JestFactory {
    constructor(swaggerObject) {
        this.swaggerObject = swaggerObject;
    }
    /**
     * This is a representation of the isValid method that will be mapped from the SwaggerConverter class.
     */
    isValid() {
        // validate the swaggerObject to check if it is a valid Swagger object
        if (this.swaggerObject === undefined ||
            this.swaggerObject.tags === undefined ||
            this.swaggerObject.paths === undefined) {
            console.error('❌ Invalid Swagger object for Jest, Swagger object must have tags and paths properties');
            return false;
        }
        if (this.swaggerObject.hasOwnProperty('openapi') &&
            this.swaggerObject.openapi.startsWith('3')) {
            console.error('❌⌛Swagger object is NOT valid for Jest, for now we only support OpenAPI 2.0');
            return false;
        }
        return true;
    }
    /**
     * This is a representation of the convert method that will be mapped from the SwaggerConverter class.
     */
    create() {
        if (!this.isValid()) {
            throw new Error('Invalid Swagger object for Jest');
        }
        const description = this.swaggerObject.info.title ||
            this.swaggerObject.info.summary ||
            this.swaggerObject.info.description ||
            'Jest tests';
        const jestInstance = new JestInstance(description);
        // instantiate the Blocks based on the tags and add them to the JestInstance
        // define the "valid" tags in the Swagger object to map to Jest describe blocks
        for (const tag of this.swaggerObject.tags) {
            console.debug('Creating JestInstance for tag', tag.name);
            // what are the paths that have this tag?
            const paths = Object.keys(this.swaggerObject.paths).filter((path) => {
                const pathItem = this.swaggerObject.paths[path];
                // we need to check if the tag is in all the operations
                // of the path and return all the paths that have the tag
                return Object.keys(pathItem).some((operation) => {
                    const operationObject = pathItem[operation];
                    return operationObject.tags.includes(tag.name);
                });
            });
            console.debug('Paths for tag', tag.name, paths);
            // ************************************************************
            // create a describe block for the tag
            const blockDescription = tag.description || tag.name + ' tests';
            const describeBlock = new Describe(blockDescription);
            // instantiate the it blocks based on the paths and add them to the describe block
            for (const path of paths) {
                // paths may have multiple operations, we handle all of them
                const pathItem = this.swaggerObject.paths[path];
                // operations in the path
                console.debug('Operations for path', path, Object.keys(pathItem));
                for (const operation in pathItem) {
                    const operationObject = pathItem[operation];
                    // check if the operation has the tag
                    if (operationObject.tags.includes(tag.name)) {
                        const itDescription = operationObject.summary || operationObject.operationId;
                        // ************************************************************
                        console.debug('Creating it block for operation', operation, itDescription);
                        const itBlock = new It(itDescription);
                        // set the method of the it block
                        itBlock.setMethod(operation);
                        describeBlock.addItBlock(itBlock);
                    }
                }
            }
            jestInstance.addDescribeBlock(describeBlock);
        }
        // what we have created? Number of describe blocks, number of it blocks
        console.log('Created JestInstance with', jestInstance.getDescribeBlocks().length, 'describe blocks');
        console.log('Created JestInstance with', jestInstance
            .getDescribeBlocks()
            .map((block) => block.itBlocks.length)
            .reduce((a, b) => a + b, 0), 'it blocks');
        return jestInstance;
    }
}
